/*----------------------------------------------*/
/*Nav Bar Style*/
/*----------------------------------------------*/
header {
  position: absolute;
  top: 0;
  z-index: 1;
  display: inline-block;
  width: 100%;
  height: 52px;
  color: #dbdbda;
  background-color: #303030;
}

header ul {
  display: inline-block;
}

