  float: left;
  padding-top: 6px;
  padding-left: 2em;
}

@media screen and (min-width: 1100px) {
  .nav-icon {
    padding-left: 0;
  }
}

.nav-list {
  position: relative;
  display: inline-block;
  float: right;
}

@media screen and (max-width: 560px) {
  .nav-list {
    display: none;
  }
}

.nav-input {
  position: relative;
  bottom: 3px;
  display: inline-block;
  float: right;
  padding: 0 16px 0 40px;
}

.nav-item {
  height: 52px;
  padding: 0 10px 0 7.2px;
  font-family: 'Lato';
  font-size: .9em;
  line-height: 52px;
  color: #dbdbda;
  letter-spacing: .06em;
  cursor: pointer;
  transition: color .2s;
}

.nav-item a {
  color: #dbdbda;
}

.nav-item:hover, .nav-item:focus,
.nav-item a:hover, .nav-item a:focus {
  color: #696966;
}

.nav-links {
  float: center;
}

.inner {
  max-width: 1100px;
  padding-right: 2em;
  padding-left: 2em;
  margin-right: auto;
  margin-left: auto;
}

.nav-dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  position: absolute;
  right: 0;
  z-index: 1;
  height: 0;
  overflow: hidden;
  opacity: 0;
  display: none;
  text-align: right;
  transition: opacity .3s;
}

.dropdown-content :hover a {
  color: #696966;
}

.dropdown-content .nav-item {
  padding-left: 12px;
  background-color: #303030;
  border-bottom: 1px solid #565656;
  line-height: normal;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  text-align: left;
}

.nav-events {
  width: 180px;
}

.nav-dropdown:hover .dropdown-content,
.nav-dropdown:focus .dropdown-content {
  height: auto;
  display: block;
  opacity: 1;
}

